.chocolate-logo-9 {
  height: 90px;
  width: 61px;
  position: relative;
  object-fit: cover;
}
.frame-child115,
.frame-child116 {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-saddlebrown-100);
  width: 12.9px;
  height: 45px;
}
.frame-child116 {
  left: 20.6px;
}
.rectangle-parent26 {
  width: 33.5px;
  height: 45px;
  position: relative;
}
.sim-conch-inner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 26px 0 0;
}
.sim-conch-icon {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: -2px;
  right: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  z-index: 1;
}
.sim-conch {
  width: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 81px var(--padding-10xl) 727px;
  box-sizing: border-box;
  gap: var(--gap-smi);
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 157px) {
  .sim-conch {
    flex-wrap: wrap;
  }
}
